body {
  color: #2c3e50;
  background: linear-gradient(135deg, #f6f9fc, #e9eff5);
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.app-container {
  text-align: center;
  padding: 30px 20px;
}

.app-container h1 {
  color: #1a237e;
  margin-bottom: 25px;
  font-size: 2.2rem;
  font-weight: 600;
}

.controls {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin-bottom: 30px;
  display: flex;
}

label {
  font-size: 1rem;
  font-weight: 500;
}

select {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 8px 12px;
  font-size: 1rem;
  transition: all .2s;
}

select:hover {
  border-color: #1a73e8;
  box-shadow: 0 0 5px #1a73e84d;
}

.coin-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 25px;
  display: flex;
}

.coin-card {
  text-align: center;
  background-color: #fff;
  border-radius: 16px;
  width: 220px;
  padding: 20px;
  transition: all .3s;
  box-shadow: 0 4px 12px #0000001a;
}

.coin-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 8px 18px #00000026;
}

.coin-card img {
  width: 70px;
  height: 70px;
  margin-bottom: 10px;
}

.coin-card h3 {
  color: #1a237e;
  margin: 10px 0 6px;
  font-size: 1.2rem;
}

.coin-card p {
  color: #555;
  margin: 4px 0;
  font-size: .9rem;
}

.loading {
  color: #1a73e8;
  margin-top: 20px;
  font-size: 1.2rem;
}

.error {
  color: red;
  margin-top: 20px;
  font-weight: bold;
}
